.historicoAtendimento {
  height: 23rem;
}

.historico__tituloDiv {
  height: 1.5rem;
  display: flex;
  margin-bottom: 1.5rem;
}

.historico__titulo {
  width: 16rem;
  height: 1.5rem;
  color: #303b5b;
  margin-bottom: 1.5rem;
  font-family: "Poppins";
  font-style: normal;
  font-weight: 700;
  font-size: 1.125rem;
}

.historico__botao {
  display: flex;
  align-items: center;
}

.historico__historicos {
  width: 100%;
  height: 20rem;
  background-color: #fff;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  gap: 1rem;
  align-items: center;
  padding: 1rem;
  border-radius: 6px;
}

.historico__historico {
  width: 100%;
  height: 18rem;
  border: 0.06rem solid rgba(237, 241, 248, 1);
}

.historico__imagemIcone {
  width: 100%;
  height: 8.5rem;
  background-image: url("../../assets/imagens/bg-historico-1.png");
  background-size: cover;
  background-repeat: no-repeat;
  display: flex;
  align-items: center;
  justify-content: center;
}
